<template>
  <div class="message">
    <div class="message-img">
      <img src="../assets/messageBoard.png" alt="message-img">
      <div class="message-font">ÁïôË®ÄÊùø</div>
    </div>
    <div class="message-con" v-loading="loading">
      <comment title="ÁïôË®Ä" :commentList="commentList" v-on:submitComment="addCommentFunc" v-on:deleteCommment="deleteCommmentFunc"></comment>
    </div>
  </div>
</template>

<script>
import comment from '@/components/layout/comment'
export default {
  name: 'messageBoard',
  components: {
    comment
  },
  data () {
    return {
      loading: true,
      commentList: [],
      url: {
        addCommentUrl: this.$wang.api.addCommentUrl,
        getCommentlistUrl: this.$wang.api.getCommentlistUrl,
        deleteCommentUrl: this.$wang.api.deleteCommentUrl
      }
    }
  },
  methods: {
    // Ëé∑ÂèñÁïôË®ÄÂàóË°®
    getCommentFunc () {
      this.$wang.ajax({
        url: this.url.getCommentlistUrl,
        param: {
          type: '1'
        }
      }).then(res => {
        this.loading = false
        this.commentList = res.data
      }).catch(() => {
        this.loading = false
      })
    },
    // Ê∑ªÂä†ÁïôË®ÄÂàóË°®
    addCommentFunc (data) {
      let param = {
        content: data,
        author: this.$store.state.userInfo._id,
        type: '1'
      }
      this.$wang.ajax({
        url: this.url.addCommentUrl,
        param
      }).then(res => {
        if (res.code === 0) {
          this.$message({
            message: 'ÁïôË®ÄÊàêÂäüÔºÅ',
            type: 'success'
          })
          this.getCommentFunc()
        } else {
          this.$message({
            message: 'ÊúçÂä°Âô®Âá∫Èîô',
            type: 'error'
          })
        }
      })
    },
    // Âà†Èô§ËØÑËÆ∫
    deleteCommmentFunc (id) {
      this.$wang.ajax({
        url: this.url.deleteCommentUrl,
        param: {
          _id: id
        }
      }).then(res => {
        if (res.code === 0) {
          this.$message({
            message: 'Âà†Èô§ËØÑËÆ∫ÊàêÂäüÔºÅ',
            type: 'success'
          })
          this.getCommentFunc()
        } else {
          this.$message({
            message: 'ÊúçÂä°Âô®Âá∫Èîô',
            type: 'error'
          })
        }
      })
    }
  },
  created () {
    this.getCommentFunc()
    let oneLever = {
      type: '1',
      content: 'ÊâãÂä®‰∏äÊä•ÈîôËØØ‰ø°ÊÅØ'
    }
    console.log(this.$wang.Sentry)
    // throw new Error(JSON.stringify(oneLever))
    this.$wang.Sentry.captureMessage('‰ªòË¥πÂäüËÉΩ', JSON.stringify(oneLever))
  }
}
</script>

